	height: 40px;
	align-items: center;
	display: flex;
	justify-content: flex-end;
	position: fixed;
	top: 0;
	width: 100%;
	background: #fff;
	z-index: 11;
	bottom: 0;
	right: 0;
	* {
		-webkit-transition: all 0.5s cubic-bezier;
		-moz-transition: all 0.5s linear;
		-o-transition: all 0.5s linear;
		transition: all 0.5s linear;
	}
}
